

<!--历史红包页面-->

<template>
    <div>
        <header id="head_top">
            <section class="head_goback" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </section>
            <section class="title_head ellipsis">
                        <span class="title_text">
                            历史优惠
                        </span>
            </section>
        </header>
        <section class="scroll_container" id="scroll_section">
            <ul class="hongbao_list_ul">
                <li class="hongbao_list_li" v-for="history in overdueBonus">
                    <section class="list_item">
                        <div class="list_item_left">
                            <span>￥</span>
                            <span>{{history.amount==4.5?4:history.amount }}</span>
                            <span>.</span>
                            <span>0</span>
                            <p>{{history.description_map.sum_condition}}</p>
                        </div>
                        <div class="list_item_right">
                            <h4>{{history.name}}</h4>
                            <p>{{history.description_map.validity_periods}} </p>
                            <p>{{history.description_map.phone}}</p>
                        </div>
                    </section>
                    <i class="el-icon-circle-close"></i>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
    export default {
        name: "hbHistory",
        data() {
            return {
                overdueBonus:[],
            }
        },
        created(){
            if (localStorage.getItem('user')){
                this.user = JSON.parse(localStorage.getItem('user'))
                this.user_id = this.user[0].user_id
                console.log(this.user_id);
                this.axios.get("promotion/v2/users/"+this.user_id+"/expired_hongbaos"+'?limit=20&offset=0', {
                    user_id:this.user_id,
                    limit:20,
                    offset:0
                }).then(res=>{
                    this.overdueBonus = res
                    console.log(res)
                }).catch(err=>{

                })
            }
        },
        methods:{
            goBack() {
                this.$router.push({
                    path:'/benefit',

                })
            }
         }
    }
</script>

